<template>
  <div class="jl-information jl-onlinebill">
    <div class="jdy-content jdy-transfer fleft jl-ad">
      <div class="jdy-content-inner-trip">
        <div class="resources-tab">
          <div></div>
          <div>
            <el-button type="default" class="btnInTab fright" @click="goBack()">返回</el-button>
          </div>
        </div>
        <!-- <div class="mt30"></div> -->
        <!-- <el-form :model="ruleForm" :inline="true" ref="ruleForm" label-width="80px" class="mt30">
            <el-row style="width: 100%;">
                <el-form-item label="" label-width="100px">
                    <el-date-picker v-model="ruleForm.dateRange" type="daterange" @change="datetimerange" placeholder="选择时间范围" style="width:100%;">
                    </el-date-picker>
                </el-form-item>
                <el-button type="primary" @click="firstSearch" class="el-button btnbg el-button--primary">搜索</el-button>
                <el-button type="default" class="btnbg fr">导出</el-button>
            </el-row>
        </el-form> -->
      </div>
      <div class="jdy-table jdy-table-padding">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column type="index" label="序号">
          </el-table-column>
          <el-table-column prop="aTitle" label="编号">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.pno}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="aTitle" label="线路名称">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.pname}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="aTitle" label="产品经理">
            <template scope="scope">
              <el-button @click="getUserInfo(scope.row.managerId)" type="text" >{{ scope.row.manager }}</el-button>
              <!-- <span class="jl-noticeTitle" @click="getUserInfo(scope.row.userId)" style="color:#20a0ff">{{scope.row.pName}}</span> -->
            </template>
          </el-table-column>
          <el-table-column prop="aTitle" label="行程天数" width="250">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.tripDays}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="aEndTime" label="操作" width="200">
            <template scope="scope">
              <el-button type="default" size="mini" class="red mleft0" @click="deletePro(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页   begin-->
      <div class="clearfix">
        <el-pagination class="fright mt20" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSizeAll" :total="tableDataTotal">
        </el-pagination>
      </div>
      <!-- 分页end-->
      <!-- 查看用户信息弹窗   begin-->
        <jdy-alert title="用户基本信息" @closeAlert="closeAlert" v-if="alertFlag" class="alertCityList showtfcity productView">
            <div class="yjk-alertInner">
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">用户账号：</span>
                        <span>{{userInfo.uAccount}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">真实姓名：</span>
                        <span>{{userInfo.uRealName}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">员工编号：</span>
                        <span>{{userInfo.uNo}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">职务：</span>
                        <span>{{userInfo.uPost}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">单位名称：</span>
                        <span>{{userInfo.cName}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">部门名称：</span>
                        <span>{{userInfo.dName}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">负责人：</span>
                        <span>{{userInfo.uDtype |chargePersonType}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">身份证：</span>
                        <span>{{userInfo.uIdcard}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">手机号：</span>
                        <span>{{userInfo.uTel}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">出生年月：</span>
                        <span>{{userInfo.uBirthday| dateFormat}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">性别：</span>
                        <span>{{userInfo.uSex | sexName}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">文化程度：</span>
                        <span>{{userInfo.degreeName}}</span>
                    </span>
                </div>
                <div>
                    <!-- <span class="infoLine">
                        <span class="infoTitle">政治面貌：</span>
                        <span>{{userInfo.uAccount}}</span>
                    </span> -->
                    <span class="infoLine">
                        <span class="infoTitle">紧急联系人：</span>
                        <span>{{userInfo.uContacts}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">地址：</span>
                        <span>{{userInfo.uAddress}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">电话：</span>
                        <span>{{userInfo.uPhone}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">微信号：</span>
                        <span>{{userInfo.uWxname}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">QQ：</span>
                        <span>{{userInfo.uQq}}</span>
                    </span>
                    <span class="infoLine">
                        <span class="infoTitle">邮箱：</span>
                        <span>{{userInfo.uEmail}}</span>
                    </span>
                </div>
                <div>
                    <span class="infoLine">
                        <span class="infoTitle">备注：</span>
                        <span>{{userInfo.uRemark}}</span>
                    </span>
                </div>
            </div>
        </jdy-alert>
      <!-- 查看用户信息弹窗  end -->
    </div>
    <!--jdy-content end-->
  </div>
</template>

<script>
import jdyAlert from "@/components/Alert";
export default {
  components: {
    jdyAlert
  },
  name: "information",
  data() {
    return {
      currentPage: 1,
      pageSizeAll: 20,
      tableDataTotal: 0,
      tableData: [
        {
          pno: 99,
          id: 1,
          pname:'销售副总',
          manager: '徐璐',
          tripDays: 8
        },
        {
          pno: 999,
          id: 2,
          pname:'销售总监',
          manager: '金璐',
          tripDays: 88
        },
        {
          pno: 999,
          id: 3,
          pname:'销售经理',
          manager: '袁俊凯',
          tripDays: 888
        },
        {
          pno: 999,
          id: 4,
          pname:'签约代理人',
          manager: '李伟',
          tripDays: 8888
        },
        {
          pno: 999,
          id: 5,
          pname:'非销售岗 ',
          manager: '姜行',
          tripDays: 88888
        }
      ],

      ruleForm: {
        dateRange: [
          new Date().setTime(new Date().getTime() + 3600 * 1000 * 24 * 1),
          new Date().setTime(new Date().getTime() + 3600 * 1000 * 24 * 16)
        ],
        beginDate: dateChange(1),
        endDate: dateChange(16),
        pageSize: 20,
        currPage: 1
      },

      //user
      alertFlag: false
    };
  },
  mounted() {
    this.getTableData();
  },
  filters:{
      degreeFormat:function(value){
       // return value.substring(0,7);
        return value
      },
      sexName: function (value) {
        return value ? '女':'男'
      },
      chargePersonType:function(value){
        const val= Number(value);
        let returnVal = '';
        if(value === 0){
         // returnVal='普通代理人'
          returnVal='';
        }
        else if(value === 1){
          returnVal='负责人+部门领导'
        }
        else if(value === 2){
          returnVal='法人'
        }
        else if(value === 3){
          returnVal='法人+部门领导'
        }
        else if(value === 4){
          returnVal='部门领导'
        }
        else if(value === 5){
          returnVal='法人+负责人+部门领导'
        }

        return returnVal;
      }
    },
  methods: {
    getTableData() {
      let param={
        currPage:1,
        pageSize:20,
        posId:this.$route.query.posId,
        companyId:this.$route.query.companyId,
        str: ""
      }
      //更新table数据
      this.$http
        .post(api_prefix + "/pExperience/productList",param)
        .then(
          response => {
            let data = response.body.body;
            this.tableData = data.list;
            this.tableDataTotal = data.total;
          },
          response => {
            console.log("出错了");
          }
        );
    },
    //currentPage 改变时会触发
    handleCurrentChange(val) {
      console.log(val, "val");
      this.ruleForm.currPage = val;
      this.getTableData();
    },
    // 返回按钮
    goBack() {
      this.$router.push({ name: "experienceProduct" });
    },
    closeAlert() {
      this.alertFlag = false;
    },
    getUserInfo(userid){
      console.log(userid,'userid')
        this.$http.get(api_prefix + 'user/get/' + userid, ).then(response => {
            if (response.data.code == 0) {
                this.userInfo=response.data.body;
                this.alertFlag=true;
                this.$nextTick(function(){
                    jdyFn.setAlertStyle('showtfcity');
                });
            }
        }, response => {
            console.log('出错了');
        });

      },
      //删除行
    deletePro(index) {
      //点击删除
      let url = api_prefix + "/pExperience/delete/" + index;
      this.$confirm("你确定要删除该内容？", "温馨提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http.get(url).then(
            response => {
              if (response.data.code == 0) {
                this.getTableData();
                setTimeout(() => {
                  this.$message({
                    showClose: true,
                    message: "删除成功",
                    type: "success"
                  });
                }, 500);
              } else {
                this.$alert(response.data.message);
              }
            },
            response => {
              console.log("出错了");
            }
          );
        })
        .catch(() => {});
    }
  }
};
</script>
<style scoped lang="less">
/* 弹窗 */
.yjk-alertInner{
    padding: 20px;
    margin-bottom:0;
    .yjk-innerTitle{
        text-align: center;
    }
    .infoLine{
        width: 49%;
        text-align: left;
        display: inline-block;
        .infoTitle{
            display: inline-block;
            width:30%;
            text-align: right;
        }
    }
}
.totelDiv{
    padding-top:10px;
}
</style>




